import React from 'react';
import * as S from './ButtonLocate.style';

function ButtonLocate() {
  const checkWeatherByGeo = () => {};

  const showLocalizeError = (error) => {
    switch (error.code) {
      case error.PERMISSION_DENIED:
        alert('Użytkownik odrzucił prośbę o geolokalizację.');
        break;

      case error.POSITION_UNAVAILABLE:
        alert('Informacje o lokalizacji są niedostępne.');
        break;

      case error.TIMEOUT:
        alert('Przekroczono czas oczekiwania na lokalizację');
        break;

      case error.UNKNOWN_ERROR:
        alert('Ups, coś poszło nie tak');
        break;
      default:
    }
  };

  const findMe = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          checkWeatherByGeo(position.coords.latitude, position.coords.longitude);
        },
        showLocalizeError,
        {
          timeout: 1000,
          maximumAge: 10000,
          enableHighAccuracy: true,
        },
      );
    } else {
      alert('Geolokalizacja nie jest obsługiwana przez tę przeglądarkę.');
    }
  };
  const handleClick = () => {
    findMe();
  };
  return <S.Button onClick={handleClick}>Locate me</S.Button>;
}

export default ButtonLocate;
